.products {
    width: 100vw;
    text-align: center;
    padding: 100px 20px;
}

.container-products {
    margin: 50px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.cards {
    background-color: #f6f6f6;
    width: calc(50% - 15px);
    padding: 20px 10px;
    margin: 0 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cards h2 {
    margin: 0;
    font-size: 32px;
}

.cards p {
    margin: 0;
    font-size: 22px;
    font-weight: bold;
}

.cards img {
    width: 80%;
    max-height: 150px;
    margin: 30px 0;
    object-fit: contain;
}

.products ul {
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.products li {
    text-align: left;
    margin: 5px;
}