
.banner {
    background-image: url('/public/assets/capa.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 80vh;
}

.txtbanner {
    height: 100%;
    width: 100%;
    padding: 100px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: aliceblue;
}

.txtbanner p, h2 {
    max-width: 500px;
    font-family: 'Anybody', cursive;
}

.txtbanner p {
    font-size: 36px;
}

.txtbanner h2 {
    font-size: 80px;
    margin: 0;
    letter-spacing: 5px;
    line-height: 70px;
}
