header div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

header img {
    width: 150px;
}

header a{
    font-size: 18px;
    color: #000;
    text-decoration: none;
    padding: 5px;
    margin: 0
}

header a:hover {
    color: red;
}




nav { 
    transition: all .2s ease-in-out; 
}  
nav:hover { 
    transform: scale(1.1); 
    margin: 5px;
}

